.footer {
	width: 100%;
	position: relative;
	bottom: 0;
	height: max-content;
	min-height: 50vh;
	line-height: 2;
	margin: 0;
	box-shadow: -1px -1px 10px #ccc;
}

.footer__title {
	text-align: center;
	padding: 30px 10px 10px 10px;
}

.footer__container {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-evenly;
	margin: 20px auto;
}

.footer__column {
	display: flex;
	flex-direction: column;
	align-items: center;
	min-width: 200px;
	line-height: 1.5;
	padding-bottom: 7px;
	margin: 10px 0;
}

.footer__column-title {
	font-size: x-large;
	font-weight: 700;
	margin-bottom: 10px;
	letter-spacing: 1.3px;
	padding: 0 5px;
}

.footer__column-subText {
	letter-spacing: 0.5px;

	font-size: 17px;
	padding: 5px 5px;
	margin: 5px 0;
	font-weight: 600;
	color: black;
	transition: all 0.2s linear;
}

.footer__column-subText:hover {
	color: #555;
}

.footer__final-line {
	position: relative;
	bottom: 0;
	font-family: "Montserrat";
	margin-bottom: 0;
	color: white;
	height: 25px;
	display: flex;
	align-items: center;
	width: 100%;
	background: rgb(250, 51, 134);
}

.copyright-text {
	margin: 0 60px;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 16px;
	letter-spacing: 1px;
}

.footer__copyright-icon {
	font-size: x-large;
	margin: 5px 7px 0 40px;
}

.footer > .footer__title .header__logo-svg > svg {
	width: 340px;
}

@media only screen and (max-width: 700px) {
	.footer__container {
		padding: 10px 50px;
	}

	.footer__column {
		margin: 10px 0;
	}
}

@media only screen and (max-width: 430px) {
	.copyright-text {
		margin: 0 10px;
	}

	.footer > .footer__title .header__logo-svg > svg {
		width: 300px;
	}
}
@media only screen and (max-width: 370px) {
	.footer > .footer__title .header__logo-svg > svg {
		width: 260px;
	}
}
